<template>
  <div class="pos">
    <el-row>
      <el-col :span='7' class="pos-order" id="orderlist">
        <el-tabs>
          <el-tab-pane label="点餐">
            <el-table :data="tableData" border style="width: 100%">
              <el-table-column prop="goodsName" label="商品" ></el-table-column>
              <el-table-column prop="count" label="数量" width="70"></el-table-column>
              <el-table-column prop="price" label="金额" width="70"></el-table-column>
              <el-table-column label="操作" width="100" fixed="right">
                <template scope="scope">
                  <el-button type="text" size="small" @click="addOrderList(scope.row)">添加</el-button>
                  <el-button type="text" size="small" @click="delSingleGood(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="挂单">
            挂点
          </el-tab-pane>
          <el-tab-pane label="外卖">
            外卖
          </el-tab-pane>
        </el-tabs>
        <div class="totlaDiv">
          <small>数量：</small><span>{{totalCount}}</span>
          <small>金额：</small><span>{{totalMoney}}</span>
        </div>
        <div class="odbar-btm">
          <el-button type="warning" >挂单</el-button>
          <el-button type="danger" @click="delAllGoods">删除</el-button>
          <el-button type="success" @click="checkOut">结账</el-button>
        </div>
      </el-col>
      <el-col :span='17'>
        <div class="often-goods">
          <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="goods in oftenGoods" @click="addOrderList(goods)" >
                <span>{{goods.goodsName}}</span>
                <span class="o-price">¥{{goods.price}}元</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-type">
            <el-tabs>
              <el-tab-pane label="汉堡">
                <ul class='cookList'>
                    <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="小食">
                <ul class='cookList'>
                    <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="饮料">
                <ul class='cookList'>
                    <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="套餐">
                <ul class='cookList'>
                    <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </el-tab-pane>
            </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'pos',
    data(){
      return {
        tableData:[],
        oftenGoods:[],
        type0Goods:[],
        type1Goods:[],
        type2Goods:[],
        type3Goods:[],
        totalMoney:0,
        totalCount:0
      }
    },
    created:function(){
      axios.get('http://jspang.com/DemoApi/oftenGoods.php')
      .then(response=>{
        this.oftenGoods = response.data;
      })
      .catch(error=>{
        alert("网络错误，请重试")
      })

      axios.get('http://jspang.com/DemoApi/typeGoods.php')
      .then(response=>{
        // console.log(response)
        this.type0Goods = response.data[0];
        this.type1Goods = response.data[1];
        this.type2Goods = response.data[2];
        this.type3Goods = response.data[3];
      })
      .catch(error=>{
        alert("网络错误，请重试")
      })

    },
    mounted: function (){
      var odbarHeight = document.body.clientHeight;
      document.getElementById('orderlist').style.height = odbarHeight+'px';
    },
    methods:{
        addOrderList(goods){
          this.totalMoney=0;
          this.totalCount=0;
          // console.log(goods)
          let isHave=false;
          for(let i=0;i<this.tableData.length;i++){
            if(this.tableData[i].goodsId == goods.goodsId){
              isHave=true;
            }
          }
          if(isHave){
            let arr = this.tableData.filter(o =>o.goodsId == goods.goodsId);
            arr[0].count++;
            // console.log(arr);
          }else{
            //不存在就推入数组
            let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
            this.tableData.push(newGoods);
          }
          this.getAllMoney()

        },
        delSingleGood(goods){
            this.tableData = this.tableData.filter(o=>o.goodsId!=goods.goodsId);
            this.getAllMoney();
        },
        delAllGoods(){
          this.tableData=[];
          this.totalCount=0;
          this.totalMoney=0;
        },
        getAllMoney(){
          this.totalCount=0;
          this.totalMoney=0;
          if(this.tableData){
            this.tableData.forEach(e=>{
                this.totalCount += e.count;
                this.totalMoney += e.price*e.count;
            })
          }
        },
        checkOut(){
          if(this.tableData != 0){
            this.tableData=[];
            this.totalCount=0;
            this.totalMoney=0;
            this.$message({
              message:'结账成功！',
              type:'success'
            });
          }else{
            this.$message.error('没有订单不能空结');
          }
        }

    }
/* eslint-enable */
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.pos-order{
  background-color: #f9fafc;
  border-right: 1px solid #c0ccda;
}
.odbar-btm{
  margin-top: 10px;
}
.title{
  height: 20px;
  border-bottom:1px solid #D3DCE6;
  background-color: #F9FAFC;
  padding:10px;
  text-align: left;
}
.often-goods-list ul li{
  list-style: none;
  float:left;
  border:1px solid #E5E9F2;
  padding:10px;
  margin:5px;
  background-color:#fff;
  border-radius: 10px;
  cursor: pointer;
}
.o-price{
  color:#58B7FF; 
}
.goods-type{
  clear: both;
}
.cookList li{
  list-style: none;
  width:23%;
  border:1px solid #E5E9F2;
  height: auot;
  overflow: hidden;
  background-color:#fff;
  padding: 2px;
  float:left;
  margin: 2px;
  cursor:pointer;
 
}
.cookList li span{
   
    display: block;
    float:left;
}
.foodImg{
   width: 40%;
}
.foodName{
   font-size: 18px;
   padding-left: 10px;
   color:brown;

}
.foodPrice{
   font-size: 16px;
   padding-left: 10px;
   padding-top:10px;
}
.totlaDiv{
  border-bottom: 1px solid #E5E9F2;
  padding: 5px;
}
.totlaDiv span{
  font-size: 24px;
  margin-right: 20px;
  color: #58B7FF;
}
</style>


